
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'sliderDetail',
components:{ XHeader },
computed:{
title(){
return this.$route.params.title;
},
list(){
return this.$store.getters.sliderDetail;
},
},
created(){
var id = this.$route.params.id;
this.$store.dispatch('getSliderDetail',id)
}
}



